// ------------------------------------
// Constants
// ------------------------------------
export const STUDIO_COUNTER_INCREMENT = 'STUDIO_COUNTER_INCREMENT'
export const STUDIO_FETCHED = 'STUDIO_FETCHED'

// ------------------------------------
// Actions
// ------------------------------------
export function increment (value = 1) {
  return {
    type: STUDIO_COUNTER_INCREMENT,
    payload: value
  }
}

/*  This is a thunk, meaning it is a function that immediately
    returns a function for lazy evaluation. It is incredibly useful for
    creating async actions, especially when combined with redux-thunk!

    NOTE: This is solely for demonstration purposes. In a real application,
    you'd probably want to dispatch an action of COUNTER_DOUBLE and let the
    reducer take care of this logic.  */

export const fetchStudio = () => {
  return (dispatch, getState) => {
    return new Promise((resolve) => {
      console.log(getState().router.locationBeforeTransitions.pathname.split("/")[2])
      dispatch({
        type: STUDIO_FETCHED,
        payload: {
          imageSrc : ['http://localhost:3000/recordingStudio1.jpg','http://localhost:3000/recordingStudio2.jpg','http://localhost:3000/recordingStudio3.jpg']
        }
      })
      resolve();
    })
  }
}

export const actions = {
  increment,
  fetchStudio
}

// ------------------------------------
// Action Handlers
// ------------------------------------
const ACTION_HANDLERS = {
  [STUDIO_COUNTER_INCREMENT]: (state, action) => {
    return Object.assign({}, state.studio,{
      studio : state.studiosList,
      fetched : true
    });
  },
  [STUDIO_FETCHED]: (state,action) => {
    return Object.assign({}, state.studio,{
      studio : action.payload,
      fetched : true
    });
  }
}

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  fetched : false,
  studio : {}
}
export default function studioReducer (state = initialState, action) {
  const handler = ACTION_HANDLERS[action.type]
  return handler ? handler(state, action) : state
}



